@import '../../Constant/breakPoint.scss';
@import '../../Constant/font.scss';
@import '../../Constant/color.scss';

.home_container {
    position: relative;
    width: 952px;
    padding-top: 30px;
    margin: auto;
}
.home_uploadFile {
    position: relative;
    display: flex;
    align-items: center;
    padding-left: 20px;
    width: 202px;
    height: 84px;
    border: 1px solid #ccc;
    cursor: pointer;
    span {
        font-size: 18px;
        margin-left: 20px;
    }
    input {
        position: absolute;
        height: 100%;
        left: 0;
        top: 0;
        opacity: 0;
        cursor: pointer;
    }
}
.home_content {
    width: 952px;
    height: 998px;
    padding: 100px 100px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    border-radius: 3px;
    box-sizing: border-box;
    margin-bottom: 20px;
}
.home_content2 {
    position: absolute;
    top: -1000px;
    left: -1000px;
    width: 952px;
    margin: auto;
    padding: 100px 100px;
    border-radius: 3px;
    box-sizing: border-box;
    margin-bottom: 20px;
}
